.carousel {
  width: 100%;
  padding-top: 20px;
  position: relative;
  overflow: hidden;
}

.carousel .carousel-container {
  white-space: nowrap;
}

.carousel .carousel-container .carousel-item {
  width: 167px;
  height: auto;
  display: inline-block;
}

@media (width >= 400px) {
  .carousel {
    padding-top: 32px;
  }

  .carousel .carousel-container .carousel-item {
    width: 251px;
  }
}

@media (width >= 650px) {
